import { Component } from '@angular/core';


import { PopoverController ,NavParams} from '@ionic/angular';

@Component({
  template: `
    <ion-list>
     
      <ion-item *ngIf="permisosorden.comentariosorden==='true'"  button (click)="close('Agregar Comentarios')">
        <ion-label>Agregar Comentarios</ion-label>
      </ion-item>
      <ion-item *ngIf="permisosorden.fotosorden==='true'" button (click)="close('Agregar Archivos')">
        <ion-label>Agregar Fotos</ion-label>
      </ion-item>
      <ion-item *ngIf="permisosorden.documentosorden==='true'" button (click)="close('Agregar Documentos')">
        <ion-label>Agregar Documentos</ion-label>
      </ion-item>
      <ion-item *ngIf="permisosorden.tareasorden==='true'" button (click)="close('Asignar Tareas segun proceso')">
        <ion-label  text-wrap>Asignar Tareas segun proceso</ion-label>
      </ion-item>
      
      <ion-item button (click)="close('Estados del proceso')">
        <ion-label  text-wrap>Estados del proceso</ion-label>
      </ion-item>

      <ion-item *ngIf="permisosorden.documentacionorden==='true'" button (click)="close('Documentación de orden')">
        <ion-label  text-wrap>Documentación de orden</ion-label>
      </ion-item>
      <ion-item *ngIf="otras==='true'" button (click)="close('Otras opciones')">
        <ion-label  text-wrap>Otras opciones</ion-label>
      </ion-item>
    </ion-list>
  `
})
export class Popover3Page {
  permisosorden: any = [];
  otras:any;
  constructor(public popoverCtrl: PopoverController, private navParams: NavParams ) {

    this.permisosorden = navParams.get('permisosorden');
    this.otras= navParams.get('otras');

  }

  support() {
    // this.app.getRootNavs()[0].push('/support');
    this.popoverCtrl.dismiss();
  }

  close(url: string) {
   
    this.popoverCtrl.dismiss({data: url});
  }
}
